.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.time-control {
  width: 50%;
  display: flex;
}

.time-control input {
  width: 100%;
  padding: 1rem 2rem;
}

.time-control p {
  padding: 1rem 2rem;

}

.play-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 45%;
}

.play-control svg {
  cursor: pointer;
}